<template>
  <div class="EatWhat">
     <div class="headerPart">
        <h1 class="myTitle"></h1>
        <p class="info"></p>
      </div>
      <div class="weui-grids" >
        <div  v-for="(grid, index) in grids" :key="grid.title">
            <a href="javascript:;" class="weui-grid" :class="{active: grid.index === activeIndex}" 
            :style="{background: grid.index === activeIndex ? hoverColor : ''}" @click=random(grid.clickable,index,grid.title)>
            <div class="weui-grid__icon">
                <img :src='"static/image/"+grid.image' alt="">
            </div>
            <p class="weui-grid__label" v-if="grid.title !== ''">{{grid.title}}</p>
            <p class="weui-grid__label" v-if="grid.title === ''">{{doingText}}</p>
        </a>
        </div>
    </div>
  </div>
</template>
<script>
// import Bus from '@/services/bus'
import untils from '@/utils/utils.js'
export default {
  props: ['userinfo'],
  data () {
    return {
      grids: [
        {title: '', image: 'food1.png', clickable: false, index: 1},
        {title: 'M', image: 'food2.png', clickable: false, index: 2},
        {title: 'K', image: 'food3.png', clickable: false, index: 3},
        {title: '', image: 'food4.png', clickable: false, index: 8},
        {title: '', image: 'begin.png', clickable: true, index: ''},
        {title: '', image: 'food6.png', clickable: false, index: 4},
        {title: '', image: 'food7.png', clickable: false, index: 7},
        {title: '', image: 'food8.png', clickable: false, index: 6},
        {title: '', image: 'food9.png', clickable: false, index: 5}
      ],
      activeIndex: 1,
      flag: false,
      timer: '',
      finish: 1,
      editIndex: '',
      hoverColor: '',  //  
      doingText: '',  // 
      doingTimer: '',
      lockRandom: false,   // 
      test: 'hahw '
    }
  },
  mounted () {
    this.$nextTick(() => {
      // 
      this.$el.querySelector('.myTitle').style.color = this.userinfo.color
      this.hoverColor = untils.hexToRgba(this.userinfo.color)
      this.$el.querySelector('.EatWhat .active').style.background = this.hoverColor
    })
  },
  methods: {
    // 
    random (con, index, food) {
      console.log('in', this.lockRandom)
      if (this.lockRandom) {
        return false
      }
      if (con === true) {
        this.lockRandom = true
        console.log('in', this.lockRandom)
        this.doingText = ''
        this.loadingText()
        var timer = setInterval(this.add, 100)
        setTimeout(() => {
          clearInterval(timer)
          this.finish = 1
          this.flag = true
          this.utimal = 9 + Math.round(Math.random() * 8)
          this.timer = setInterval(this.add, 200 * (Math.random() + 1))
        }, 4000 + (Math.random() + 1) * 100)
      } else {
        this.$router.push({path: 'editFood'})
        window.index = index // index
      }
    },
    // 
    loadingText () {
      this.doingTimer = setInterval(() => {
        this.doingText = this.doingText.length >= 6 ? this.doingText.replace(/\./g, '') : this.doingText + '.'
      }, 500)
    },
    add () {
      if (this.activeIndex === 8) {
        this.activeIndex = 1
      } else {
        this.activeIndex++
      }
      if (this.flag) {
        if (this.finish === this.utimal) {
          clearInterval(this.timer)
          clearInterval(this.doingTimer)
          this.flag = false
          this.lockRandom = false
          this.doingText = ''
        } else {
          this.finish++
        }
      }
    }
  },
  created () {
    let foodList = JSON.parse(localStorage.getItem('food') || '[]')
    if (foodList.length === 0) {
      this.grids = [
        {title: '', image: 'food1.png', clickable: false, index: 1},
        {title: 'M', image: 'food2.png', clickable: false, index: 2},
        {title: 'K', image: 'food3.png', clickable: false, index: 3},
        {title: '', image: 'food4.png', clickable: false, index: 8},
        {title: '', image: 'begin.png', clickable: true, index: ''},
        {title: '', image: 'food6.png', clickable: false, index: 4},
        {title: '', image: 'food7.png', clickable: false, index: 7},
        {title: '', image: 'food8.png', clickable: false, index: 6},
        {title: '', image: 'food9.png', clickable: false, index: 5}
      ]
      localStorage.setItem('food', JSON.stringify(this.grids))
    } else {
      this.grids = foodList
    }
  }
}
</script>

<style lang="scss" rel="stylesheet/scss">
    .EatWhat {
        .headerPart {
            padding-bottom: 2rem;
            color: red;
            text-align: center;
            .myTitle {
                color: #3cc51f;
                font-size: 3.2rem;
                font-weight: 400;
            }
            .info {
                color: gray;
                font-size: 1.6rem;
            }
        }
        .active {
            background: #E8C2A8;
        }
    }
</style>



