<template>
  <div class="when">
     <div class="headerPart">
        <h1 class="myTitle">{{weekNum}}</h1>
        <p class="info">~~~happy</p>
      </div>
  </div>
</template>
<script>
  export default {
    props: ['userinfo'],
    data () {
      return {
        weekNum: ''
      }
    },
    methods: {
      getWeek () {
        let week = []
        week[0] = ''
        week[1] = ''
        week[2] = ''
        week[3] = ''
        week[4] = ''
        week[5] = ''
        week[6] = ''
        let d = new Date()
        let weekNow = d.getDay()
        return week[weekNow]
      }
    },
    mounted () {
      this.$nextTick(() => {
        this.weekNum = this.getWeek()
        // 
        this.$el.querySelector('.myTitle').style.color = this.userinfo.color
      })
    }
  }
</script> 
<style lang="scss" rel="stylesheet/scss">
    .when {
        .headerPart {
            padding: 4rem 0;
            color: red;
            text-align: center;
            .myTitle {
                color: #3cc51f;
                font-size: 34px;
                font-weight: 400;
            }
            .info {
                color: gray;
                font-size: 1.7rem;
            }
        }
    }
</style>



