<template>
  <div class="selectIcon">
      <div class="tabPart">
          <div class="part" :class="{hightlight: highlight === 'output'}"  @click="tab('output')"></div>
          <div class="part" :class="{hightlight: highlight === 'income'}" @click="tab('income')"></div>
      </div>
       <div class="consumeList">
          <!-- <div class="weui-cells__title" style="font-size:1rem;"><span><span>  </span> <span style="float:right"> 62.00</span></span></div> -->
          <div class="weui-cells" style="font-size:1.1rem;">
            <div class="weui-cell" v-for="(key, index) in iconList.length" :key="index" >
                <div class="weui-cell__hd" >
                  <span class="shoppingicon"><i class="iconfont" :class="iconList[index].iconClass"></i></span></div>
                <div class="weui-cell__bd" @click="confirmDelete(index)">
                    <span>{{iconList[index].title}}</span>
                </div>
                <div class="weui-cell__ft"><input class="weui-switch" type="checkbox" v-model="iconList[index].type"></div>
            </div>
           </div>
      </div>
      <div class="fixBottom spacing confirm slideIn dataPlan">
        <input href="javascript:;" class="weui-btn weui-btn_primary" placeholder="" @mouseleave="showDialog" v-model="newItem">
      </div>
      <!--  -->
      <div class="js_dialog hideDialog" id="iosDialog1"  v-bind:class="{ showDialog: confirmShow }" @click="hideDialog">
        <div class="weui-mask"></div>
        <div class="weui-dialog" >
            <div class="weui-dialog__hd" v-show="dialogType === 'add'"><strong class="weui-dialog__title"></strong></div>
            <div class="weui-dialog__hd" v-show="dialogType === 'delete'"><strong class="weui-dialog__title"></strong></div>
            <div class="weui-dialog__bd"  v-show="dialogType === 'add'"></div>
            <div class="weui-dialog__bd" v-show="dialogType === 'delete'"></div>
            <div class="weui-dialog__ft">
                <a href="javascript:;" class="weui-dialog__btn weui-dialog__btn_default" @click="hideDialog"></a>
                <a  v-show="dialogType === 'add'" href="javascript:;" class="weui-dialog__btn weui-dialog__btn_primary" @click="addItems"></a>
                <a v-show="dialogType === 'delete'" href="javascript:;" class="weui-dialog__btn weui-dialog__btn_primary" @click="deleteItems"></a>
            </div>
        </div>
      </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      highlight: 'output',
      iconList: [],
      outputIcon: [],
      showIcon: [],
      incomeIcon: [],
      confirmShow: false,
      newItem: '',
      activeIndex: '',
      dialogType: ''
    }
  },
  watch: {
    highlight (val) {
      console.log('aaaaa')
      if (val === 'output') {
        this.iconList = this.outputIcon
      } else {
        this.iconList = this.incomeIcon
      }
    },
    outputIcon: {
      handler: function (val) { localStorage.setItem('outputIcon', JSON.stringify(val)) },
      deep: true
    },
    incomeIcon: {
      handler: function (val) { localStorage.setItem('incomeIcon', JSON.stringify(val)) },
      deep: true
    }
  },
  methods: {
    tab (name) {
      this.highlight = name
      console.log(this.outputIcon)
      // this.iconList = this.outputIcon
    },
    fetchData () {
      console.log('jajjaja')
    },
    showDialog (index) {
      this.confirmShow = true
      this.dialogType = 'add'
    },
    hideDialog () {
      this.confirmShow = false
    },
    addItems () {
      let newItemObj = {name: 'star', iconClass: 'icon-xing', type: 'checked'}
      newItemObj.title = this.newItem
      if (this.highlight === 'output') {
        this.outputIcon.unshift(newItemObj)
      } else {
        this.incomeIcon.unshift(newItemObj)
      }
    },
    confirmDelete (index) {
      this.confirmShow = true
      this.activeIndex = index
      this.dialogType = 'delete'
    },
    deleteItems () {
      if (this.highlight === 'output') {
        this.outputIcon.splice(this.activeIndex, 1)
      } else {
        this.incomeIcon.splice(this.activeIndex, 1)
      }
    },
    // iconlist  
    getIconList () {
      this.outputIcon = JSON.parse(localStorage.getItem('outputIcon') || '[]')
      this.incomeIcon = JSON.parse(localStorage.getItem('incomeIcon') || '[]')
      console.log(this.incomeIcon.length, '')
      console.log(this.outputIcon, this.incomeIcon, 'icon', 'icon')
      if (this.incomeIcon.length === 0) {
        this.incomeIcon = [
      {name: 'pay', title: '', iconClass: 'icon-zhifuxinshui', type: true},
      {name: 'getmoney', title: '', iconClass: 'icon-jiangxuejinv', type: true},
      {name: 'shorttime', title: '', iconClass: 'icon-jianzhizhongdiangong', type: true},
      {name: 'rate', title: '', iconClass: 'icon-touzihouhuodeshouyi', type: true}]
        this.outputIcon = [
      {name: 'shopping', title: '', iconClass: 'icon-gouwu', type: true},
      {name: 'money', title: '', iconClass: 'icon-licai', type: true},
      {name: 'traffic', title: '', iconClass: 'icon-jiaotong', type: true},
      {name: 'fun', title: '', iconClass: 'icon-yule', type: true},
      {name: 'meal', title: '', iconClass: 'icon-icon', type: true},
      {name: 'travel', title: '', iconClass: 'icon-lvyou', type: true},
      {name: 'medical', title: '', iconClass: 'icon-yiliao', type: true},
      {name: 'specialMoney', title: '', iconClass: 'icon-lijin', type: true},
      {name: 'shopping', title: '', iconClass: 'icon-gouwu', type: true},
      {name: 'money', title: '', iconClass: 'icon-licai', type: true},
      {name: 'traffic', title: '', iconClass: 'icon-jiaotong', type: true},
      {name: 'fun', title: '', iconClass: 'icon-yule', type: true},
      {name: 'meal', title: '', iconClass: 'icon-icon', type: true},
      {name: 'travel', title: '', iconClass: 'icon-lvyou', type: true},
      {name: 'medical', title: '', iconClass: 'icon-yiliao', type: true},
      {name: 'specialMoney', title: '', iconClass: 'icon-lijin', type: true},
      {name: 'beauty', title: '', iconClass: 'icon-meirong', type: true}]
        localStorage.setItem('outputIcon', JSON.stringify(this.outputIcon))
        localStorage.setItem('incomeIcon', JSON.stringify(this.incomeIcon))
      }
      this.highlight = 'output'
      this.iconList = this.outputIcon
    }
  },
  mounted () {
    // iconlist
    setTimeout(() => this.getIconList(), 1000)
  }
}
</script>
<style lang="scss" rel="stylesheet/scss">
  .selectIcon {
    padding: 4rem 2rem;
    box-sizing: border-box;
      .tabPart {
        display: flex;
        padding: 1.2rem 2rem;
        color: white;
        position: fixed;
        top: 4rem;
        z-index: 20;
        width: 100%;
        background: #a9f0a9;
        box-sizing: border-box;
        left: 0;
      .part {
        flex: 1;
        border: 1px solid #1AAD19;
        // background: red;
        color: #1AAD19;
        text-align: center;   
      }
      .hightlight {
        background: #1AAD19; 
        color: white;   
      }
    }
    .shoppingicon {
      display: inline-block;
      width: 4rem;
      height: 4rem;
      text-align: center;
      line-height: 4rem;
      border-radius: 50%;
      background: #f8f5f5;
      border: 1px solid #f8f5f5;
      margin:0.5rem 1rem 0.5rem -1.5rem;
      color: gray;
        i {
          font-size: 2.5rem;
          color: green;
        }
      }
      .consumeList {
        margin-top: 4rem;
      }
      .fixBottom {
        position: fixed;
        bottom: 0px; 
        width: 100%;
        left: 0px;   
      }
      .showDialog {
            opacity: 1!important;
            display: block!important;
        }
      .hideDialog {
        opacity: 0;
        display: none;
      }
  }
</style>



