<template>
  <div id="app">
    <!--  -->
    <mt-spinner class="g-loading" type="double-bounce" v-show="loading" :color="loadingColor"></mt-spinner>
    <app-header>
      <i class="iconfont icon-menu" @click="toggleShowMenu"></i>
      <!-- <mt-button icon="back" @click="toggleShowMenu">back</mt-button> -->
    </app-header>
    <!--  -->
      <my-menu :my-visible.sync = "visible">
      <!--  -->
      <!-- <p slot='toggleBtn'></p> -->
      <template slot="menu-title"></template>
      <menu-item route='/'><i slot='icon' class=' iconfont icon-403010'></i></menu-item>
      <menu-item route='/DatePlan'><i slot='icon' class=' iconfont icon-403010'></i>DatePlan</menu-item>
      <menu-item route='/EatWhat'><i slot='icon'  class=' iconfont icon-chi'></i></menu-item>
      <menu-item route='/memo'><i slot='icon'  class=' iconfont icon-beiwanglu'></i></menu-item>
      <menu-item route='/when'><i slot='icon'  class=' iconfont icon-fangjia'></i></menu-item>
      <menu-item route='/icon'><i slot='icon'  class=' iconfont icon-pinrenpinkongxin'></i></menu-item>
      <!-- <menu-item route='/mirror'><i slot='icon'  class=' iconfont icon-jingzi'></i></menu-item>  -->
      <my-submenu>
        <i slot="icon" class=' iconfont icon-jizhang'></i><template slot="submenu-title"></i></template>
        <menu-item route='/money'><i slot="icon" class=' fa fa-circle-o'></i></menu-item>
        <menu-item route='/moneyRecord'><i slot="icon" class='fa fa-circle-o'></i></menu-item>
        <my-submenu>
          <i slot="icon" class='fa fa-circle-o'></i><template slot="submenu-title"></template>
          <menu-item><i slot="icon" class=' fa fa-circle-o'></i></menu-item>
          <menu-item><i slot="icon" class=' fa fa-circle-o'></i></menu-item>
        </my-submenu>
         <my-submenu>
          <i slot="icon" class='fa fa-circle-o'></i><template slot="submenu-title"></template>
          <menu-item><i slot="icon" class=' fa fa-circle-o'></i></menu-item>
          <menu-item><i slot="icon" class='fa fa-circle-o'></i></menu-item>
          <my-submenu>
            <i slot="icon" class='fa fa-circle-o'></i><template slot="submenu-title"></template>
            <menu-item><i slot="icon" class=' fa fa-circle-o'></i>dddddd</menu-item>
            <menu-item><i slot="icon" class=' fa fa-circle-o'></i>ddddddddddddd</menu-item>
          </my-submenu>
        </my-submenu>
      </my-submenu>
    </my-menu>
    <router-view :userinfo="userinfo" @update-userinfo="updateUserinfo"></router-view>
  </div>
</template>

<script>
import appHeader from '@/components/header'

export default {
  name: 'app',
  components: {
    appHeader
  },
  data () {
    return {
      visible: false,  // 
      loading: false,
      userinfo: {
        headUrl: './static/img/head.jpg',  // 
        color: ''
      },
      loadingColor: '#26a2ff'  // loading 
    }
  },
  created () {
    // 
    this.$bus.$on('set-theme', (color) => {
      this.loadingColor = color
      this.userinfo.color = color
    })
  },
  mounted () {
    // loading
    this.getAllData()
    this.$bus.$on('loading', () => {
      this.loading = true
    })
    this.$bus.$on('unload', () => {
      this.loading = false
    })
  },
  methods: {
    // 
    toggleShowMenu () {
      console.log('')
      this.visible = true
    },
    setTheme () {
      this.loadingColor = this.userinfo.color
    },
    getAllData () {
      //   
      let color = localStorage.getItem('themeColor') || '#f04134'
      color && this.$bus.$emit('set-theme', color)
      this.userinfo.headUrl = localStorage.getItem('headImg') || this.userinfo.headUrl
    },
    // 
    updateUserinfo (obj) {
      this.userinfo = Object.assign(this.userinfo, obj)
    }
  }
}
</script>

<style lang="scss" rel="stylesheet/scss">
html {
  font-size: 62.5%;
}
body {
  position: absolute;
  margin: 0;
  padding: 0;
  width: 100%;
  min-height: 100%;
}
#app {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
             "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei",
             SimSun, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  .g-loading {
    position: fixed;
    top: 50%;
    left: 50%;
    margin-left: -14px;
    margin-top: -14px;
  }
}
</style>



